/* Board story specific styles */
.board-story-container {
  width: 400px;
  max-width: 100%;
  margin: 0 auto;
}

.board-story-wide {
  width: 600px;
  max-width: 100%;
  margin: 0 auto;
}

.board-story-narrow {
  width: 300px;
  max-width: 100%;
  margin: 0 auto;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .board-story-container,
  .board-story-wide,
  .board-story-narrow {
    width: 100%;
    padding: 0 20px;
  }
}
